.big-container {
  background-color: rgba(218, 191, 255, 0.89);
}

::-webkit-scrollbar {
  width: 20px;
  height: 20px;
}

::-webkit-scrollbar-track {
  border: coral;
  background-color: rgba(186, 129, 248, 0.89);
  border: 1px solid lightgray;
  border-radius: 1.5em;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(164, 85, 249, 0.89);
  border: 1px solid rgba(226, 206, 247, 0.89);
  border-radius: 1.5em;
}
